body {
    overflow:hidden;
}

main {
    display: flex;
    width: 100vw;
    height: 100vh; 
}

/* Collage from https://www.w3schools.com/howto/howto_css_image_grid_responsive.asp */
.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 1px;
  overflow-y: auto;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
  background-color: var(--background-color);
  max-width: 30%;
  margin-right: 20px;
}

.row::-webkit-scrollbar {
  display: none;
}

/* Create four equal columns that sits next to each other */
.column {
  flex: 32%;
  max-width: 32%;
  padding: 0 0.5px;
}

.column img {
  margin-top: 0.5px;
  vertical-align: middle;
  width: 100%;
  opacity: 0.2;
  transition: 1s;
}

.column img:hover {
  transition: 500ms;
  transform: scale(1.3);
  z-index: 2;
  border-radius: 5%;
  opacity: 1;
}

.focused {
  transform: scale(1.3);
  z-index: 2;
  border-radius: 5%;
  opacity: 1 !important;
}

.text-area {
  height: 100%;
  overflow-y: auto;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
}

.text-area::-webkit-scrollbar {
  display: none;
}

.text-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: -10px; /* adjust as needed */
  width: 10px; /* adjust as needed */
  height: 100%;
  background: linear-gradient(to right, transparent, #fff);
  filter: blur(5px); /* adjust as needed */
}


.about-section {
  padding-top: 40px;
}

.about-heading, .about-intro {
  font-family: 'Ubuntu', sans-serif;
  color: var(--spare-color);
}

.about-heading {
  text-align: center;
  /* color: var(--base-color1); */
}

.about-intro {
  margin-top: 10px;
}

.highlight {
  color: var(--base-color1);
  font-weight: bolder;
}

#toggle-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  left: 30%;
  background: var(--base-color1);
  opacity: 0.8;
  z-index: 5;
  cursor: pointer;
  transition: 300ms;
}

#toggle-button:hover {
  opacity: 1;
}

.about-animation-play {
  display: none !important;
  font-size: 4rem;
}

.about-animation-pause {
  font-size: 2rem;
  color: var(--background-color);
}

.back-slide {
    background-color: var(--background-color);
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    height: 100%;
    width: 160px;
    transition: 0.4s;
}

.back-slide:hover {
  width: 200px;
}

.about-arrow {
  font-size: 2rem;
  color: var(--spare-color);
  opacity: 0.4;
  transform: scale(-1);
  transition: 0.4s;
}

.back-slide:hover .about-arrow {
  opacity: 1;
  transform: scale(-1.5);
}

